.app-name {
  font-size: 1rem;
  text-align: center;
}

.footer {
  min-height: 60px;
  text-align: center;
  padding-top: 10px;
  background-color: #bec4c8;
  margin-left: 250px;
}

.side-bar {
  flex: 0 0 auto;
  width: 250px;
  position: fixed;
  top: 56px;
  bottom: 0;
  left: 0;
  overflow-y: auto;
  background-color: #f8f9fa;
}

.pagination {
  justify-content: left;
  padding: 0.25rem;
}

.page-item .page-link {
  color: #202124;
  background-color: #e8e8e8;
  border: none;
}

.page-item.active .page-link {
  color: #000;
  background-color: #c0c0c0;
  border-color: #c0c0c0;
}

.main-content {
  padding: 15px;
  margin-left: 250px;
  min-height: 100vh;
}

.fonte-doida {
  font-size: 13px;
}

.nav-brand {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  font-size: 1rem;
}

.margin {
  margin-left: 10px;
}

.loader {
  width: 20px;
  --b: 3px;
  aspect-ratio: 1;
  border-radius: 50%;
  padding: 1px;
  background: conic-gradient(#0000 10%, #f03355) content-box;
  -webkit-mask-composite: destination-in;
  mask-composite: intersect;
  animation: l4 1s infinite steps(10);
}

@keyframes l4 {
  to {
      transform: rotate(1turn)
  }
}

.upper {
  text-transform: uppercase;
}

.h4 {
  font-size: calc(1.26rem + .12vw);
}

.btn-outline-secondary {
  --bs-btn-color: #598392;
  --bs-btn-border-color: #598392;
}

.btn-primary {
  --bs-btn-color: #fff;
  --bs-btn-bg: #598392;
  --bs-btn-border-color: #598392;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #598392;
  --bs-btn-hover-border-color: #598392;
  --bs-btn-focus-shadow-rgb: 255,147,60;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #598392;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0,0,0,0.125);
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #598392;
  --bs-btn-disabled-border-color: #598392;
}
